      margin: 0 100px;
  }

  .header {
      height: auto;
  }

  .header-down {
      display: block;
      margin-top: 85px;
  }

  .design-and-werst {
      font-size: 36px;
      padding-top: 30px;
  }

  .lorem-ipsum {
      text-align: center;
      width: auto;
  }

  .header-btn {
      display: flex;
      justify-content: center;
      margin-top: 43px;
      padding-bottom: 34px;
  }

  .header-frame {
      display: flex;
      justify-content: center;
  }

  .design-and-werst {
      display: flex;
      justify-content: center;
  }

  .premany-imgs img {
