  }

  .design-and-werst {
      margin-top: 10px;
      text-align: center;
  }

  .header-down {
      display: block;
      margin-top: 50px;
  }

  .lorem-ipsum {
      width: auto;
      text-align: center;
  }

  .imgs-projects {
      display: block;
  }

  .imgs-projects img {
      width: auto;
  }

  .header-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 55px;
  }

  .my-skills-prime {
      display: block;
  }

  .premany-imgs img {
      width: 95%;
      margin-left: 1%;
  }

  .microsoft-imgs img {
      width: 90%;
  }

  .img-frame {
      width: 60%;
      margin-left: 23px;
  }
  .my-information {
      display: block;
  }

  .my-vk {
      padding-top: 0;
