      margin: 0 15px;
  }

  .header {
      height: auto;
  }

  .header-frame {
      padding-top: 0;
  }

  .design-and-werst {
      margin-top: 10px;
      text-align: center;
  }

  .header-down {
      display: block;
      margin-top: 50px;
  }

  .lorem-ipsum {
      margin-top: 0;
      width: auto;
      text-align: center;
      padding: 10px 0;
  }

  .imgs-projects {
      display: block;
  }

  .imgs-projects img {
      width: 80%;
  }

  .header-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 0px;
      padding-top: 5px;
      padding-bottom: 10px;
  }
}

@media (max-width: 1070px) {
  .burger-menu {
      display: block;
  }
